<template>
  <div class="body">
    <p><b>A</b>llen's blog</p>
    <ul id="navigateBar">
      <li class="singleNavigate" v-for="item in items">
        <router-link :to="item.link">{{ item.content }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          {
            link: '/',
            content: 'Main'

          },
          {
            link: '/article',
            content: 'Article'
          },
          {
            link: '/lab',
            content: 'Lab'
          }
        ]
      }
    }
  }
</script>

<style scoped>
.body {
  height: 680px;
  background-color: rgb(220,220,220);
  text-align: center;
  margin: 0;
  padding: 0;
}

div>p {
  position: relative;
  display: inline-block;
  margin-top: 18%;
  font-size:50px;
}

#navigateBar {
  width:300px;/*980px*/
  height: 35px;
  margin-left: auto;
  margin-right:auto;
}
ul .singleNavigate {
  list-style: none;
  float:left;
}

.singleNavigate a {
  width:80px;/*80px*/
  height:35px;/*28px*/
  line-height:35px;/*28px*/
  font-size:18px;/*12*/
  display:block;/*a*/
  text-align:center;/**/
  text-decoration:none; /**/
}

.singleNavigate a:hover{
  width:78px;
  height:33px;
  line-height:28px;
  font-size: 25px;
}
</style>



