<template>
  <div id='head'>
    <img src="../assets/banner.jpg">
    <ul id="navigateBar">
      <li class="singleNavigate" v-for="item in items">
        <router-link :to="item.link">{{ item.content }}</router-link>
      </li>
    </ul>
    <router-view name='homePage'> </router-view>
  </div>

</template>

<script>
export default {
  name: 'head',
  data () {
    return {
      msg: 'i am allen, a software engineer!',
      items: [
        {
          link: '/',
          content: 'Main'
        },
        {
          link: '/article',
          content: 'Article'
        },
        {
          link: '/lab',
          content: 'Lab'
        },
        {
          link: '/manage',
          content: 'Manage'
        }
      ]
    }
  }
}
</script>

<style scoped>
#head {
  width: 100%;
}
img {
  width: 100%;
  height: 300px;
}
#sign {
  position: absolute;
  color: white;
  top: 5px;
  left: 10px;
}
#navigateBar {
  width:auto;/*980px*/
  margin:0px auto 0px auto;/*margin:0 auto*/
  height: 35px;
}
ul .singleNavigate {
  list-style: none;
  float:left;
}

.singleNavigate a {
  width:80px;/*80px*/
  height:35px;/*28px*/
  line-height:35px;/*28px*/
  color:#000;/**/
  font-size:17px;/*12*/
  display:block;/*a*/
  text-align:center;/**/
  text-decoration:none; /**/
  padding-left: 10px;
}

.singleNavigate a:hover, .singleNavigate a:active {
  width:78px;
  height:33px;
  line-height:28px;
  border: solid #000;
  border-width: 0 0 1px 0;
}
</style>



